<template>
  <div :class="data.mine ? 'chat-item chat-item-mine': 'chat-item'">
	  <img :src="data.fromUser.avatar" alt="avatar" class="avatar"/>
	  <div class="right-content">
		  <p class="nickname one-line">{{data.fromUser.nickname}}</p>
		  <div v-if="data.content.type === 'str'" class="chat-text left-arrow">{{data.content.value}}</div>
		  <img 
		  	v-if="data.content.type === 'pic'" 
			@click="showimage(data.content.value)" 
			:src="getimageurl(data.content.value.url)"
			:style="imgOneStyle(data.content.value)"
			alt="pic" 
			class="chat-image"/>
	  </div>
  </div>
</template>
<script>
export default {
	name: "chatCell",
	props: {
		data: Object
	},
	methods: {
		showImage (item) {
			new Slider({
				list: [{
					img: item.url,
					height: item.size.height,
					width: item.size.width
				}],
				page: 0
			})
		},
		getImageUrl (val) {
			let _url = val + '?x-oss-process=image/resize,l_300'
			return _url
		},
		pxtovw (px) {
			return (px / 375 * 100) + 'vw'
		}
	},
	computed: {
		imgOneStyle () {
			return item => {
				let height = null
				let width = null
				if(item.size.height > item.size.width) {
					height = Math.min(120, item.size.height)
					// 根据比例设置宽度
					width = height * item.size.width / item.size.height
				} else {
					width = Math.min(120, item.size.width)
					height = width * item.size.height / item.size.width
				}
				return {
					height: this.pxtovw(height),
					width: this.pxtovw(width)
				}
			}
		}
	}
}
</script>

